import { useState, useEffect } from "react";

/**
 * A wrapper for useState that persists state chages to either localStorage or sessionStorage
 *
 * @param {any} initialState - the initial state of the form - defaults to {}
 * @param {string} formKey - the name of the form. It's important to choose a unique name to avoid collisions with any other form a user may have open
 * @param {localStorage|sessionStorage} storageType - the type of storage to use. Check out https://developer.mozilla.org/en-US/docs/Web/API/Window/sessionStorage for a primer on the difference
 *
 * @return {Object[]} an array with two entries:
 *         {Object} state - the current state object
 *         {Fuction} setState - a function for setting the state object
 */
export const useStorageState = (
    initialState,
    formKey,
    storageType = localStorage
) => {
    const existingState = JSON.parse(storageType.getItem(formKey));
    const [state, setState] = useState(existingState || initialState);

    // Persist all state changes to localStorage
    useEffect(() => {
        storageType.setItem(formKey, JSON.stringify(state));
    }, [state, formKey, storageType]);

    return [state, setState];
};
