const CHECKBOX_VALUES = ["", "", "", "", "", "", "", ""];

/**
 * Toggle the current emoji value for the given day
 */
export const toggleCheckbox = (setCheckboxValue, currValue) => {
    setCheckboxValue(
        CHECKBOX_VALUES[
            (CHECKBOX_VALUES.indexOf(currValue) + 1) % CHECKBOX_VALUES.length
        ]
    );
};

/**
 * Clear local storage
 */
export const resetState = () => {
    localStorage.clear();
    document.location.reload();
};
