import React, { useState, useRef, useEffect } from "react";
import { TextField, Button } from "@mui/material";
import { DatePicker } from "@mui/x-date-pickers";
import { addDays, format, parseISO } from "date-fns";

import { useStorageState } from "./stateHelpers/customHooks";
import {
    defaultSettings,
    toggleCheckbox,
    resetState,
} from "./stateHelpers/helpers";

import "./App.scss";

function WeekDays() {
    return (
        <div className="weekdays">
            <div>
                <p>Sunday</p>
            </div>
            <div>
                <p>Monday</p>
            </div>
            <div>
                <p>Tuesday</p>
            </div>
            <div>
                <p>Wednesday</p>
            </div>
            <div>
                <p>Thursday</p>
            </div>
            <div>
                <p>Friday</p>
            </div>
            <div>
                <p>Saturday</p>
            </div>
        </div>
    );
}

function CalCell({
    cellText,
    setCellText,
    cellCheckboxValue,
    setCheckboxValue,
    date,
}) {
    const [isEditing, setIsEditing] = useState(false);
    const inputRef = useRef();

    useEffect(() => {
        inputRef.current?.focus();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [inputRef.current]);

    const setTextInputRef = (element) => {
        inputRef.current = element;
    };

    const startEditing = (e) => {
        if (e?.target?.id === "checkbox") {
            return;
        }
        setIsEditing(true);
    };

    const handleCheckboxClick = (currCheckboxValue) => {
        toggleCheckbox(
            (newCheckboxValue) => setCheckboxValue(newCheckboxValue),
            currCheckboxValue
        );
    };

    return (
        <div
            className="cal-cell"
            onClick={startEditing}
            onKeyDown={(e) => {
                if (e.key === "Escape") {
                    setIsEditing(false);
                } else if (e.key !== "Tab") {
                    // if anything beside escape or tab, start typing right away
                    if (!isEditing) {
                        startEditing(null);
                    }
                }
            }}
            tabIndex="0"
        >
            <div className="cell-date">{format(date, "MMM d")}</div>

            {!isEditing && <p>{cellText}</p>}
            {isEditing && (
                <TextField
                    value={cellText}
                    onChange={(e) => setCellText(e.target.value)}
                    onBlur={() => setIsEditing(false)}
                    inputRef={setTextInputRef}
                    autoFocus
                    multiline
                />
            )}
            <div
                id="checkbox"
                className="cell-checkbox"
                onClick={() => handleCheckboxClick(cellCheckboxValue)}
            >
                {cellCheckboxValue}
            </div>
        </div>
    );
}

function CalRow({ weekNumber, sundayDate }) {
    const [weekState, setWeekState] = useStorageState(
        defaultSettings[`week-${weekNumber}`] || {},
        `week-${weekNumber}`
    );

    return (
        <div className="cal-row">
            {Array.from({ length: 7 }, (_, i) => {
                const cellText = weekState[`day-${i}`];
                const cellCheckbox = weekState[`day-${i}-checkbox`];
                return (
                    <CalCell
                        key={i}
                        cellText={cellText || ""}
                        setCellText={(value) => {
                            setWeekState({ ...weekState, [`day-${i}`]: value });
                        }}
                        cellCheckboxValue={cellCheckbox}
                        setCheckboxValue={(value) =>
                            setWeekState({
                                ...weekState,
                                [`day-${i}-checkbox`]: value,
                            })
                        }
                        date={addDays(sundayDate, i)}
                    />
                );
            })}
        </div>
    );
}

function CalPage({ title, numWeeks, monthNumber, initialDate }) {
    const dateRange = `${format(initialDate, "MMMM d")}  ${format(
        addDays(initialDate, 7 * numWeeks - 1),
        "MMMM d"
    )}`;
    return (
        <div
            className="cal-page"
            style={{ gridTemplateRows: `120px 40px ${numWeeks * 100}px` }}
        >
            <header className="cal-header">
                <h1>{title}</h1>
                <h2 className="cal-date-range">{dateRange}</h2>
            </header>
            <WeekDays />
            <div
                className="cal-body"
                style={{ gridTemplateRows: `repeat(${numWeeks}, 1fr)` }}
            >
                {Array.from({ length: numWeeks }, (_, i) => {
                    return (
                        <CalRow
                            key={i}
                            weekNumber={i + 5 * monthNumber}
                            sundayDate={addDays(initialDate, 7 * i)}
                        />
                    );
                })}
            </div>
        </div>
    );
}

function App() {
    const [totalWeeks, setTotalWeeks] = useStorageState(
        defaultSettings.totalWeeks,
        "totalWeeks"
    );
    const numMonths = Math.ceil(totalWeeks / 5);

    const [startDate, setStartDate] = useStorageState(
        defaultSettings.startDate,
        "startDate"
    );
    const dateObj = startDate instanceof Date ? startDate : parseISO(startDate);
    const [inputDate, setInputDate] = useState(dateObj);

    const [title, setTitle] = useStorageState(defaultSettings.title, "title");

    return (
        <div className="App">
            <div className="settings">
                <h1>Settings</h1>
                <div className="input-row">
                    <TextField
                        label="Title"
                        value={title}
                        onChange={({ target: { value } }) => {
                            setTitle(value);
                        }}
                        style={{ width: "100%" }}
                    />
                </div>
                <div className="input-row">
                    <TextField
                        label="Number of weeks"
                        value={totalWeeks}
                        onChange={({ target: { value } }) => {
                            setTotalWeeks(value);
                        }}
                        inputProps={{ inputMode: "numeric", pattern: "[0-9]*" }}
                    />
                    <DatePicker
                        label="Start date"
                        value={inputDate}
                        onChange={(newValue) => {
                            setInputDate(newValue);
                            console.log("newValue", newValue, typeof newValue);
                            if (newValue instanceof Date && !isNaN(newValue)) {
                                setStartDate(newValue);
                            }
                        }}
                        renderInput={(params) => <TextField {...params} />}
                    />
                </div>
                <div className="input-row">
                    <Button onClick={resetState} variant="contained">
                        Reset to default calendar
                    </Button>
                </div>
            </div>

            {Array.from({ length: numMonths }, (_, i) => {
                const remainingWeeks = totalWeeks - i * 5;
                return (
                    <CalPage
                        key={i}
                        title={title}
                        numWeeks={Math.min(remainingWeeks, 5)}
                        monthNumber={i}
                        initialDate={addDays(dateObj, i * 7 * 5)}
                    />
                );
            })}
        </div>
    );
}

export default App;
